@import url(https://maxcdn.bootstrapcdn.com/bootstrap/3.3.7/css/bootstrap.min.css);
@import url(https://maxcdn.bootstrapcdn.com/bootstrap/3.3.7/css/bootstrap-theme.min.css);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #ECECEC !important;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App{
  padding: 30px;
}

/*.ant-upload{
  display: inline-table !important;
}*/

.editorName,.editorCaption{
  width: calc(100% - 115px) !important;
  margin-left: 3px !important;
}

.editorTime,.editorPrivacy{
  width: calc(50% - 60px) !important;
  margin-left: 3px !important;
  margin-top: 10px !important;
}

/* you can make up upload button and sample style by using stylesheets */

.ant-upload.ant-upload-select-picture-card{
  float: left;
}

.ant-upload-select-picture-card i {
  font-size: 32px;
  color: #999;
}

.ant-upload-select-picture-card .ant-upload-text {
  margin-top: 8px;
  color: #666;
}

/* tile uploaded pictures */
.upload-list-inline .ant-upload-list-item {
  float: left;
  width: 200px;
  margin-right: 8px;
}
.upload-list-inline .ant-upload-animate-enter {
  -webkit-animation-name: uploadAnimateInlineIn;
          animation-name: uploadAnimateInlineIn;
}
.upload-list-inline .ant-upload-animate-leave {
  -webkit-animation-name: uploadAnimateInlineOut;
          animation-name: uploadAnimateInlineOut;
}

.App .hideEditorBtn{
  display: none;
  margin: 0.5em auto;
}


@media screen and (max-width: 768px) {
  .App {
      padding: 0px;
  }
  .editor{
    margin: 1em 0;
  }
  .editorTime,.editorPrivacy{
    width: calc(100% - 115px) !important;
    margin-left: 3px !important;
  }
  .editorCaption{
    margin-left: 115px !important;
  }
  .App .hideEditorBtn{
    display: block;
  }
}

.ant-card-body{
  padding: 10px !important;
}

.postInfo{
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 1em;
}

.basicInfo{
  display: flex;
  align-items: center
}

.basicInfo .details{
  margin-left: 8px;
}

.basicInfo .details span,.basicInfo .details img,.basicInfo .details h4{
  cursor: pointer;
}

.basicInfo .details span:hover {
  -webkit-text-decoration-line: underline;
          text-decoration-line: underline;
}

.furtherInfo{
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-top: 5px;
  cursor: pointer;
}

.furtherInfo span{
  margin: 2px;
  height: 4px;
  width: 4px;
  border-radius: 50%;
  background: #737880;
}

.furtherInfo:hover > span{
  background: black;
}

.mainPost .caption p {
  overflow-wrap: break-word;
}

.postStatistics .totalReactions span{
  margin-left: 3px;
  font-size: 13px;
}

.postInteractBtns{
  position: relative;
}

.cmntBtn, .shareBtn, .likeBtn{
  border: none;
  outline: none;
  background-color: transparent;
  text-align: center;
  font-size: 16px;
  width: calc(100% - 66.67%);
  font-weight: 500;
  padding: 5px 0;
}

.likeBtn{
  display: inline-block;
  cursor: pointer;
}

.likeBtn button{
  padding: 0;
  margin: 0;
  border: 0;
  background: 0;
  outline: none;
  width: 100%;
  height: 100%;
}

.cmntBtn:hover, .shareBtn:hover, .likeBtn:hover {
  background-color:#f1f1f1;
}

