.emojies{
  position: absolute;
  padding: 5px 5px 0;
  border-radius: 20px;
  box-shadow: 8px 9px 22px -8px rgba(0,0,0,0.77);
  /*top: -57px;*/
  left: -7px;
  background: #fff;
  -webkit-animation: up ease-out;
          animation: up ease-out;
  -webkit-animation-duration: 0.3s;
          animation-duration: 0.3s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

@-webkit-keyframes up {
  0%   {top: -40px; opacity: 0;}
  100% {top: -47px; opacity: 1;}
}

@keyframes up {
  0%   {top: -40px; opacity: 0;}
  100% {top: -47px; opacity: 1;}
}

.emoji{
  margin: 0 3px;
  width: 40px;
  cursor: pointer;
  display: inline-block;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}

.emoji:hover {
  -webkit-transform: scale(1.4);
          transform: scale(1.4);
}

@media screen and (max-width: 768px) {
  .post{
    margin-bottom: 1em;
  }
}
html,
body,
#root,
.App {
  height: 100%;
}

/* Images Component CSS */

.react-fb-image-grid {
  margin: auto;
}

.react-fb-image-grid * {
  margin: 0;
  box-sizing: border-box;
  padding: 0;
  /* cursor: pointer; */
}

.col-md-4 {
  width: 33.33333333%;
}

.border {
  border: none;
  border-radius: 0;
}

.background {
  background-size: cover !important;
  background-position: center !important;
  background-repeat: no-repeat !important;
}

.height-one {
  height: 100%;
  width: 100% !important;
}

.height-two {
  height: 65%;
  width: 100% !important;
}

.cover {
  background-color: #222;
  opacity: 0.8;
  position: absolute;
  right: 0;
  top: 0;
  left: 0;
  bottom: 0;
}

.cover-text {
  right: 0;
  left: 0;
  bottom: 0;
  color: white;
  font-size: 1.2em !important;
  position: absolute;
  top: 50%;
  -webkit-transform: translate(0%, -50%);
  transform: translate(0%, -50%);
  text-align: center;
}

.cover-text > p {
  margin: 0;
  position: absolute;
  font-size: 1.5em !important;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.slide {
  height: 0;
  bottom: 100%;
  -webkit-transition: 0.4s ease;
  transition: 0.4s ease;
  overflow: hidden;
}

.border:hover .slide {
  bottom: 0;
  height: auto;
}

.border:hover .animate-text {
  top: 62%;
}

.beautify {
  border: 2px solid white;
  border-radius: 6px;
}

.cover-beautify {
  border-radius: 6px;
}
